.test {
    margin: 20px 0;

    .items {
        .row-flex();

        .item {
            min-height: 100px;
            .col-padding();

        }

        .item-left {
            background: #ccc;
            .size(8);
            padding-left: 0;
            margin-left: @offset_one_side;

        }

        .item-right {
            background: #f90;
            .size(16);
            padding-right: 0;
            margin-right: @offset_one_side;

        }

    }
}

footer {
    .items {
        .row-flex();

        .item {
            .col();
            .size(8);
            .size-sm(24);


            background: #aaa;
            min-height: 200px;

            .row {
                .row-flex();

                .xs-block( {
                    justify-content: center
                }

                );

                div {
                    .col();
                    .size(24);
                    .size-sm(8);
                    .size-xs(20);

                    padding: 5px;
                    margin-bottom: 5px;
                    background: #f50;
                }
            }
        }
    }
}